import {
  animate,
  animateChild,
  group,
  query,
  style,
  transition,
  trigger
} from '@angular/animations';

export const tabRouteAnim = trigger('tabRouteAnim', [
  transition('* <=> *', [
    query('.nav-button-container', style({ position: 'absolute', opacity: 0 }), { optional: true }),
    query(':enter, :leave', style({ position: 'absolute', top: '0rem', left: '0.95rem' }), {
      optional: true
    }),
    group([
      query(':enter', [style({ opacity: 0 }), animate('300ms ease-in', style({ opacity: 1 }))], {
        optional: true
      }),
      query(':leave', [style({ opacity: 1 }), animate('300ms ease-out', style({ opacity: 0 }))], {
        optional: true
      })
    ])
  ])
]);

// More a dissolve than a fade
export const routeAnim = trigger('routeAnim', [
  transition('* <=> *', [
    style({ position: 'relative' }),
    query('.nav-button-container', style({ position: 'absolute', opacity: 0 }), { optional: true }),
    query(
      ':enter, :leave',
      [
        style({
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%'
        })
      ],
      { optional: true }
    ),
    // query(':enter', style({ opacity: 0}), { optional: true }),
    query(':leave', animateChild(), { optional: true }),
    group([
      query(':leave', animate('300ms ease-out', style({ opacity: 0 })), { optional: true })
      // query(':enter', animate('300ms ease-in',  style({ opacity: 1 })), { optional: true })
    ]),
    query(':enter', animateChild(), { optional: true })
  ])
]);

export const eeSetupAnim = trigger('eeSetupAnim', [
  transition('* <=> EmployeeSetup', [
    style({ position: 'relative' }),
    query('.nav-button-container', style({ position: 'absolute', opacity: 0 }), { optional: true }),
    query(
      ':enter, :leave',
      [
        style({
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%'
        })
      ],
      { optional: true }
    ),
    query(':enter', style({ opacity: 0 }), { optional: true }),
    query(':leave', animateChild(), { optional: true }),
    group([
      query(':leave', animate('300ms ease-out', style({ opacity: 0 })), { optional: true }),
      query(':enter', animate('300ms ease-in', style({ opacity: 1 })), { optional: true })
    ]),
    query(':enter', animateChild(), { optional: true })
  ])
]);
